* {
  box-sizing:border-box
  
}

body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;  
  background-color: #46529d;
  color:#fff;

}

.text-center{
  text-align: center;
}
.container{
  width: 60%;
  margin: 10% auto;
}

.ListItems{
  background-color: #fff;
  color:#46529d;
}
.ListItems p{
  color:#46529d;
  text-align: center;
  margin: 0;
  padding: 10px 0;
}
.ListItems span{
  display: inline-block;
  text-align: center;
  padding: 10px 0;
  font-size: 15px;
  border: 0.3px solid #ccc;

}
.ListItems span.title{
  background-color: #2ebaee;
  color: #fff;
}
.ListItems span.name ,
form input[type=text]{
  width: 60%;
}
.ListItems span.age ,
form input[type=number]{
  width: 30%;
}
.ListItems span.action ,
form input[type=submit]{
  width: 10%;
}
form input{
  padding: 10px 0;
  background-color: #ebebeb;
  border: transparent;
  border: 0.3px solid #ccc;
}
form input[type=submit]{
  background-color: #2ebaee;
  border: 0.3px solid #ccc;
  color:#fff;
  cursor: pointer;
}
.ListItems span.icon{
  color:#2ebaee;
  cursor: pointer;
}